<template>
    <div>
        <h5>Inspections</h5>
        <b-input-group class="mt-1">
        <b-form-input
            v-model="filterSearch"
            placeholder="Search..."
            class="ml-1 mr-1 shadow-sm"
            type="search"
          ></b-form-input>
          <b-pagination
            class="ml-2 mr-2 shadow-sm"
            v-model="currentPage"
            :total-rows="rows"
            :per-page="perPage"
            aria-controls="mainTable"
          ></b-pagination>
        </b-input-group>
        <div   v-if="loading==1" class="text-center text-primary mt-1">
            <b-spinner class="align-middle mr-2"></b-spinner>loading
        </div>
        <b-card v-if="loading==0" class="mb-5 mt-2">
            <b-table
            class="shadow-sm"
            style="font-size:70%;"
            responsive
            id="inspections"
            :fields="$constants().dashboardInspectionTableFields"
            :items="itemsInspection"
            :per-page="perPage"
            :current-page="currentPage"
            :filter="filterSearch"
            :filter-function="filterTable"
            @filtered="onFiltered"
            hover
            >
            <template #cell(action)="row" align="center">
                <b-button-group class="col-md-5" align="center" size="sm">
                    <b-button
                        size="sm"
                        variant="outline-info"
                        @click="row.toggleDetails"
                        class="actionButton ml-4"
                        >Details
                        </b-button>
                </b-button-group>
             </template>
             <template #row-details="row">
                <b-row class="ml-3 mr-3" style="background-color:antiquewhite;" >
                    <b-col class="ml-3 mt-3 mb-3 mr-3"  v-show="row.item.defects.length > 0 && row.item.type == 'Single'">
                        <b>Defects</b>
                        <b-table  
                        style="font-size:90%;"
                        responsive
                        id="defects"
                        :fields="$constants().dashboardDefectSingleTableFields"
                        :items="row.item.defects"
                        small
                        >
                        </b-table>
                    </b-col>
                    <b-col class="ml-3 mt-3 mb-3 mr-3"  v-show="row.item.defects.length > 0 && row.item.type == 'Bulk'">
                        <b>Defects</b>
                        <b-table  
                        style="font-size:90%;"
                        responsive
                        id="defects"
                        :fields="$constants().dashboardDefectBulkTableFields"
                        :items="row.item.defects"
                        small
                        >
                        </b-table>
                    </b-col>
                    <b-col class="ml-3 mt-3 mb-3 mr-3"  v-show="row.item.reworks.length > 0">
                        <b >Reworks</b>
                        <b-table
                        style="font-size:90%;"
                        responsive
                        id="reworks"
                        :fields="$constants().dashboardReworkTableFields"
                        :items="row.item.reworks"
                
                        small
                        >
                        </b-table>
                    </b-col>
                </b-row>
            </template>
            <template #cell(inspectionDate)="row" align="center">
                {{ removeTime(row.item.inspectionDate) }}
            </template>
            <template #cell(inspectors)="row" align="center">
                <b-table
                style="font-size:90%;"
                responsive
                id="inspectors"
                :fields="$constants().dashboardInspectorTableFields"
                :items="row.item.inspectors"
                thead-class="d-none"
                small
                >
                </b-table>
            </template>
            </b-table>
        </b-card>
    </div>
  </template>
  
  <script>
import EreportService from '../services/ereport.service';

  export default {
    name: 'DashBoardResults',
    props:['searchTerm'],
    data() {
      return {
        loading:0,
        perPage: 10,
        currentPage: 1,
        valor: '',
        mostrarDetalleDefects:[],
        mostrarDetalleReworks:[],
        itemsInspection:[],
        filterSearch: '',
        filteredItems:[]
      };
    },
    methods: {
        onFiltered(itemsF)
        {
            this.filteredItems = itemsF;
        },
        eliminarDiacriticos(texto) {
            return texto.normalize('NFD').replace(/[\u0300-\u036f]/g, '');
        },
        filterTable(row, filter) {
         if(row.jobInspected.jobNumber != null)
         {
            if (
                row.jobInspected.jobNumber.includes(filter) ||
                row.jobInspected.jobNumber.toLowerCase().includes(filter.toLowerCase()) ||
                this.eliminarDiacriticos(row.jobInspected.jobNumber.toLowerCase()).includes(
                this.eliminarDiacriticos(filter.toLowerCase())
                ) 
                
            ) {
                return true;
            } else {
                return false;
            }
        }
        else{
            return false;
        }
        },
        cambioDetalleDefects(index)
        {
            this.mostrarDetalleDefects.splice(index, 1, !this.mostrarDetalleDefects[index]);
        },
        cambioDetalleReworks(index)
        {
            this.mostrarDetalleReworks.splice(index, 1, !this.mostrarDetalleReworks[index]);
        },
        removeTime(date) {

            console.log(date);
            date = new Date(date);
            date = date.toLocaleString();
            console.log(date);
            /*var divisor = date.split('T');
            if (divisor.length > 0) {
                return new Date(divisor[0]);
            } else {
                return new Date(date);
            }*/
            return date;
        },
        getData()
        { 
            this.loading = 1;

            EreportService.getData('reporting/inspection').then(data => {
                this.itemsInspection = data;
                this.filteredItems = data;

                for(let i = 0; i < this.itemsInspection.length;i++)
                {
                    this.mostrarDetalleDefects[i] = false;
                    this.mostrarDetalleReworks[i] = false;
                }

                this.loading = 0;
            }).catch(error => this.$parent.catchError(error));

            console.log(new Date("2023-03-31T16:57:28.331Z"));
        }
    },
    computed: {
      currentUser() {
        return this.$store.state.auth.user;
      },
      rows() {
        return this.filteredItems.length;
      }
    },
    mounted() {
      if (!this.currentUser) {
        this.$router.push('/login');
      }
      this.getData();
      console.log(this.searchTerm);
      this.filterSearch = this.searchTerm;
    },
    components: {}
  };
  </script>